// =============== IMPORT FILES
@import '_varsAndMixins';

// =============== PAGE STYLES
// ............... all pages
  html, input, textarea, button, select {
    background: #242424;
    color: #b6b6b6;
  }

  .test-results {
    @include test-format;
  }
  div.test-results {
    border: 1px dashed red;
    background: rgba(0, 0, 0, .1);
  }

// body {
